<!-- -->

<template> 
    <!---->
    <el-tree v-loading="loading" 
            :data="data" show-checkbox 
            :expand-on-click-node="false" 
            @node-click="handleNodeClick"
            @node-expand="handleNodeExpand" 
            :highlight-current="selectable"
            :default-checked-keys="defaultCheckedkeys"
            node-key="id"
            :props="defaultProps"
            ref="tree" >
    </el-tree>
</template>

<script>
    import govService from "../../../services/gov/govService.js"
    import treeUtils from '../../../utils/treeUtils'

    export default{
        props: {
            onNodeClick: Function,
            value: Array,
            defaultCheckedkeys:Array,
        },
        data () {
            return {
                data: this.value,
                loading: false,
                selectable: true, // 
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        created () {
            console.log(1111,this.defaultCheckedkeys);
            
            this.getData({id : 'tree', type :'course', filter : true , pid :0 , level:-1, pagesize:-1}).then(ret=>{ 
            //    ret.data.forEach(v => {
            //         this.data.push({
            //             id: v.id,
            //             label: v.name,
            //             // children: v.level>2 ? null : [{
            //             //     label: '',
            //             //     value: -1,
            //             // }]  //
            //         })
            //    })
               this.data = ret.data.map(v => {
                   return {
                        id: v.id,
                        label: v.name,
                        data: v,
                        name: v.name,
                    }
               })
               console.log(this.data)
               this.loading = false
               xmview.setContentLoading(false)
            })
        },
        methods: {
            // 
            getData (params) {
                return govService.getSelectList(params)
            },

            handleNodeExpand (data, node, nodeDom) { //  
                // children [...] 
    
                this.getData({id : 'tree', type :'course', filter : true , pid :node.data.value , level:-1, pagesize:-1}).then(ret=>{
                    let arr = []
                    
                    ret.data.forEach(v => {
                        arr.push({
                            data: v,
                            label: v.name,
                            name: v.name,
                            id: v.id,
                            // children: v.level>2 ? null : [{ 
                            //     label: '',
                            //     value: -1,
                            // }]  //
                        })
                })
                
                node.data.children = arr
                this.loading = false
                xmview.setContentLoading(false)
                })
            },
            handleNodeClick (data, node, store) { //
                // console.log(node.data)
                // if (node.data.ended) return
                    //     let currItem = treeUtils.findItem(this.data, node.data, 'value')   // 
                    //     var arr = []
                    //     this.getData({
                    //         pid: data.data.id, 
                    //         level:-1,
                    //     }).then((ret) => {
                    //         var arr = ret.map(v=>{
                    //             v.label = v.name
                    //             v.value = v.id
                    //             v.children = v.ended ? null : [] //nulgl 
                    //             return v
                    //         })
                    //         currItem.children = arr
                    //         this.loading = false
                    //         xmview.setContentLoading(false)
                    //    })

                 // this.$emit('onNodeClick', {data, node, store})
                this.onNodeClick(1, data, node, store) 

                //   
                if (data.value == 0) return
                this.selectable = true
            },
            // 
            clearSelected () {
                this.selectable = false
            },

            removeItem (item, parent) {
                // children 
                if (!parent.data.children) {
                    this.data = this.data.filter((curr) => {
                        return curr.value != item.value
                    })
                } else {
                    parent.data.children = parent.data.children.filter((curr) => {
                        return curr.value != item.value
                    })

                    if (parent.data.children.length < 1) parent.data.children = null
                }
                //   
                this.$emit('input', this.data)
            },
            setCurrVal (val) { //
                if (val === this.data) return
                this.data = val
                this.$emit('input', val)
            },
 
        },
    }

      function findItem(arr,key,value){
        arr.forEach(item=>{
            if(item[key] == value ){
                return item
            }
        })
    }
</script>


